#landing span {
  display: block;
  word-wrap: break-word;
}
#landing .social-links {
  display: flex;
  justify-content: space-between;
  width: 30vw;
  margin-top: 5%;
}
#landing .social-links i {
  font-size: 5em;
  color: rgba(214, 202, 202, 0.808);
}
#landing .social-links i:hover {
  color: rgb(255, 255, 255);
}
.content {
  color: white;
  height: auto;
  width: 100%;
  text-align: center;
  background: #b92b27; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.content h1 {
  margin: 0;
}
.link::after {
  content: "";
  display: block;
  position: relative;
  top: -17.5px;
  width: 0;
  height: 2px;
  background-color: white;
  transition: width 0.3s;
}

.link:hover::after {
  width: 100%;
  transition: width 0.3s;
}

div.mdl-layout__drawer-button {
  display: none;
}
@media screen and (max-width: 1024px) {
  #hamMenu {
    display: block;
  }
  div.mdl-layout__drawer-button {
    color: white;
  }
  #landing h1 {
    font-size: 10vw;
  }
  #landing .social-links {
    width: 30vw;
  }
  #landing .social-links i {
    font-size: 4.5em;
  }
}

@media screen and (max-width: 780px) {
  #hamMenu {
    position: sticky;
  }
  .mdl-layout__header-row .mdl-navigation {
    display: none;
  }
  .mdl-layout__header-row > * {
    width: 100%;
    text-align: right;
  }
  span.mdl-layout__title,
  span.mdl-layout-title {
    font-size: 15px;
  }
  div.mdl-layout__drawer-button {
    display: block;
  }
  #landing .social-links {
    width: 30vw;
  }
  #landing .social-links i {
    font-size: 3em;
  }
}

@media screen and (max-width: 480px) {
  span.mdl-layout__title,
  span.mdl-layout-title {
    font-size: 1.25rem;
  }
  div.mdl-layout__drawer {
    width: 240px;
  }
  #landing h1 {
    font-size: 50px;
  }
  #landing .social-links {
    width: 50vw;
  }
  #landing .social-links i {
    font-size: 50px;
  }
}
@media screen and (max-width: 300px) {
  span.mdl-layout__title,
  span.mdl-layout-title {
    font-size: 12.5px;
  }
  div.mdl-layout__drawer {
    width: 160px;
  }
  #landing h1 {
    font-size: 35px;
  }
  #landing .social-links {
    width: 50vw;
  }
  #landing .social-links i {
    font-size: 30px;
  }
}
